import Vue from 'vue'
import Vuex from 'vuex'
import actions from './action'
import mutations from './mutations.js'
import getter from './getter'
import state from './state'

Vue.use(Vuex)

const store = new Vuex.Store({
    actions,
    mutations,
    getter,
    state
})

export default store



